@use './theme/variables' as *;
@use './theme/mixins' as *;
@import-normalize;

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  font-size: 10px;
}

body {
  height: 100%;
  font-size: 1.6rem;
  color: $primary-col;
}

button {
  font-family: inherit;
}

ul {
  list-style: none;
}

#root {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.container {
  @include container;
}

.main {
  flex: 1 1 auto;
}
