@use '../../theme/variables' as *;
@use '../../theme/mixins' as *;

.queries {
  margin-bottom: 4rem;
  &__title {
    margin-bottom: 3rem;
    font-size: 3.2rem;
  }
  &__field {
    margin: 1rem 0 1rem 2rem;
  }
  &__description {
    padding: 2rem 1rem;
    border-radius: 1.2rem;
    font-size: 1.2rem;
    background-color: $primary-col;
    color: $white-col;
    li {
      &:not(:first-child) {
        margin-top: 1rem;
      }
      span {
        font-weight: 700;
        text-transform: uppercase;
      }
    }
    @include media(md) {
      padding: 2rem 0 2rem 4rem;
      font-size: 1.6rem;
    }
  }
  @include media(md) {
    margin-bottom: 8rem;
  }
}
