@use '../../theme/variables' as *;
@use '../../theme/mixins' as *;

.try-it {
  padding-top: 5rem;
  padding-bottom: 5rem;
  font-size: 1.8rem;
  &__title {
    font-size: 3.2rem;
  }
  &__description {
    margin-top: 2rem;
  }
  &__code {
    &-wrapper {
      padding: 2.5rem;
      margin-top: 3rem;
      border-radius: 1.2rem;
      font-size: 1rem;
      background-color: #1e1e1e;
      overflow-x: scroll;
      @include media(md) {
        font-size: 1.4rem;
      }
      @include media(lg) {
        font-size: 1.8rem;
      }
    }
    &__brackets {
      color: #ffd601;
      &--purple {
        color: #d970d7;
      }
    }
    &__operator {
      color: #569cd6;
    }
    &__method {
      color: #9ddcfe;
      &--yellow {
        color: #dcdcaa;
      }
    }
    &__string {
      color: #ce9177;
    }
    &__coma {
      color: $grey-col;
    }
  }
  &__button {
    @include button;
    margin-top: 3rem;
    color: $white-col;
    background-color: $primary-col;
  }
  @include media(md) {
    padding-top: 15rem;
    padding-bottom: 15rem;
  }
}
