@use '../../theme/variables' as *;
@use '../../theme/mixins' as *;

.header {
  box-shadow: $regular-shadow;
  &__navbar {
    padding: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include media(sm) {
      padding-right: 0;
      padding-left: 0;
    }
  }
  &__logo {
    font-size: 2.1rem;
    font-weight: 700;
    letter-spacing: 0;
    @include media(sm) {
      letter-spacing: 0.2rem;
    }
  }
  &__button {
    font-size: 1.8rem;
    color: inherit;
    text-decoration: none;
  }
}
