// Media breakpoints
$breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
);

// Containers widths
$container-widths: (
  xs: 100%,
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
);

// Colors
$primary-col: #006633;
$grey-col: #cccccc;
$black-col: #000000;
$white-col: #ffffff;

// Shadows
$light-shadow: rgba(0, 0, 0, 0.05) 0 0.1rem 0.2rem 0;
$regular-shadow: rgba(0, 0, 0, 0.12) 0 0.1rem 0.3rem,
  rgba(0, 0, 0, 0.24) 0 0.1rem 0.2rem;
$bold-shadow: rgba(0, 0, 0, 0.19) 0 1rem 2rem,
  rgba(0, 0, 0, 0.23) 0 0.6rem 0.6rem;
