@use './variables' as *;

@mixin button {
  display: inline-block;
  position: relative;
  cursor: pointer;
  height: 3.5rem;
  line-height: 3.5rem;
  padding: 0 1.5rem;
  color: #424242;
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: 0.08rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle;
  white-space: nowrap;
  outline: none;
  border: none;
  user-select: none;
  border-radius: 2px;
  transition: all 0.3s ease-out;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.225);
  &:hover {
    text-decoration: none;
    box-shadow: 0 0.4rem 1rem 0 rgba(0, 0, 0, 0.225);
  }
  &.disabled,
  &[disabled] {
    cursor: default !important;
    color: #9e9e9e !important;
    box-shadow: none !important;
    background-color: #cdcdcd !important;
    &:hover {
      background-color: #cdcdcd !important;
    }
  }
}

@mixin media($size: xs) {
  @media (min-width: #{map-get($breakpoints, $size)}) {
    @content;
  }
}

@mixin container {
  $keys: map-keys($container-widths);
  @each $key in $keys {
    @include media($key) {
      max-width: #{map-get($container-widths, $key)};
    }
  }
  margin-right: auto;
  margin-left: auto;
  padding-right: 1rem;
  padding-left: 1rem;
  transition: max-width 0.5s ease-in-out;
}
