@use '../../theme/mixins' as *;

.routes {
  margin-bottom: 4rem;
  &__title {
    font-size: 3.2rem;
  }
  &__note {
    margin: 3rem 0 1rem 2.4rem;
    font-size: 1.4rem;
    span {
      font-weight: 700;
    }
  }
  &__lists {
    padding: 2rem;
    display: flex;
    overflow-x: scroll;
    &__links {
      @extend .routes__lists__methods;
      margin-left: 4rem;
      @include media(md) {
        margin-left: 10rem;
      }
    }
    &__methods {
      li {
        margin-left: 0.5rem;
        &:not(:first-child) {
          margin-top: 1rem;
        }
      }
    }
  }
  @include media(md) {
    margin-bottom: 8rem;
  }
}
