@use '../../theme/mixins' as *;

.resources {
  margin-bottom: 4rem;
  &__title {
    font-size: 3.2rem;
    margin-bottom: 2rem;
  }
  &__lists {
    padding: 2rem;
    display: flex;
    &__links {
      li {
        &:not(:first-child) {
          margin-top: 1rem;
        }
      }
    }
    &__description {
      @extend .resources__lists__links;
      margin-left: 5rem;
    }
  }
  @include media(md) {
    margin-bottom: 8rem;
  }
}
