@use '../../theme/variables' as *;
@use '../../theme/mixins' as *;

.banner {
  background-color: $primary-col;
  &__inner {
    padding: 15rem 0;
    text-align: center;
    color: $white-col;
    @include media(sm) {
      text-align: left;
    }
  }
  &__title {
    margin-bottom: 6rem;
    font-size: 4rem;
    font-weight: 400;
    @include media(sm) {
      font-size: 5rem;
    }
    @include media(md) {
      font-size: 6rem;
    }
  }
  &__text {
    margin-bottom: 3rem;
    font-size: 1.6rem;
    @include media(md) {
      font-size: 2.5rem;
    }
  }
}
